<template>
   <div id="common-box">
        <div class="common-form">
            <h4>查询</h4>
            <div class="common-item">
                <div class="common-item-1">
                    <div class="common-item-icon">
                        <img src="../../../assets/grade.png" alt="">
                    </div>
                    <div class="common-item-title">
                        <router-link to="/Index/Query/QueryGrade">成绩查询</router-link>
                    </div>
                </div>
                <div class="common-item-1">
                    <div class="common-item-icon">
                        <img src="../../../assets/card.png" alt="">
                    </div>
                    <div class="common-item-title">
                        <router-link to="/Index/Query/MyTest">已报考信息</router-link>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            userinfo:{}
        }
    },
    methods:{
        getstudentinfo(){
            let that=this;
            this.$api.studentinfo({}).then(res=>{
                if(res.err_code === 100){
                    that.userinfo=res.data.info;
                }
            })
        }
    },
    created(){
        this.getstudentinfo();
    }
}
</script>

